angular.module('bonitasoft.ui.i18n').factory('i18n', ['gettextCatalog', '$cookies', '$window', '$timeout', '$rootScope', function (gettextCatalog, $cookies, $window, $timeout, $rootScope) {

  // Portal locales normalized into standard locales
  var normalization = [
    {'fr': 'fr-FR'},
    {'en': 'en-US'},
    {"es": 'es-ES'},
    {"it": 'it-IT'},
    {"de": 'de-DE'},
    {"pt_BR": 'pt-BR'}];

  function normalize(locale) {
    return normalization.reduce(function (acc, language) {
      return language[acc] || acc;
    }, locale);
  }

  return {
    init: function init() {

      var language = normalize($cookies.BOS_Locale
        || $window.navigator.language
        || $window.navigator.userLanguage // IE
        || 'en-US');

      gettextCatalog.setCurrentLanguage(language);
      gettextCatalog.loadRemote("assets/json/localization.json");
    }
  }
}]);
